#home {
  position: relative;
  // background: url("../../assets/bgIMG.png");
  // background-size: cover;
  // background-repeat: repeat;
  // background-position: center;

  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;

  padding: 6rem 2rem 0;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  @media screen and (max-width: 2000px) {
    width: 100%;
    margin-right: 0rem;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  .badge-cmp,
  .tag-cmp {
    padding: 2rem 2.5rem;
    border: var(--white-color);
    border-radius: 15px;
    width: auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }

  .tag-cmp {
    padding: 1.5rem 2rem;
    flex-direction: column;
    margin-top: 1rem;
    margin-bottom: 3.5rem;

    p {
      width: 100%;
      text-transform: uppercase;
      letter-spacing: 3px;
      font-weight: 500;
    }
  }

  span {
    font-size: 2.5rem;
  }

  @media screen and (min-width: 2000px) {
    font-size: 5rem;
  }
  @media screen and (max-width: 1200px) {
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 1rem;

  div {
    width: 100px;
    height: 100px;
    border-radius: 50%;

    background: var(--white-color);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-in-out;

    img {
      width: 60%;
      height: 60%;
    }

    &:hover {
      box-shadow: 0 0 25px whitesmoke;
      transform: scale(1.05);  
    }
  }

  div:nth-child(1) {
    width: 100px;
    height: 100px;
  }

  div:nth-child(2) {
    margin: 1.75rem;
    width: 100px;
    height: 100px;
  }

  div:nth-child(3) {
    width: 100px;
    height: 100px;
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(1) {
      width: 175px;
      height: 175px;
    }

    div:nth-child(2) {
      width: 175px;
      height: 175px;
    }

    div:nth-child(3) {
      width: 175px;
      height: 175px;
    }
  }

  @media screen and (max-width: 500px) {
    div:nth-child(1) {
      width: 75px;
      height: 75px;
    }

    div:nth-child(2) {
      width: 75px;
      height: 75px;
    }

    div:nth-child(3) {
      width: 75px;
      height: 75px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;
  }
}

.app__header-img {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  

  .app__header-imgProfile {
    width: 80%;
    object-fit: contain;
    z-index: 1;
    animation: float 6s ease-in-out infinite;
  }

  .app__header-imgBg {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 75%;
    object-fit: contain;
    animation: float 6s ease-in-out infinite;
  }

  @media screen and (max-width: 1200px) {
    margin-top: -5rem;
    margin-bottom: 2rem;
    width: 80%;
  }
  @media screen and (max-width: 800px) {
    width: 100%;
  }
}



@keyframes float {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-20px);
	}
	100% {
		transform: translatey(0px);
	}
}